
.day {
  padding: 10px 10px 80px 10px;
}
.day .title {
    text-align: left;
    font-weight: 700;
    font-size: 16px;
    line-height: 32px;
    margin: 10px 0;
    display: -webkit-box;
    overflow: hidden;
    text-overflow: ellipsis;
    word-wrap: break-word;
    white-space: normal !important;
    -webkit-line-clamp: 1;
}
.day .dayNewsItem {
    height: 100%;
    width: 100%;
    overflow: hidden;
}
.day .dayNewsItem p img {
      width: 100%;
}
.day .dayNewsItem h3 {
      color: #000;
      text-align: left;
      padding: 10px 0;
      font-size: 16px;
      font-weight: 700;
}
.day .dayNewsItem ul {
      color: #000;
      text-align: left;
      font-size: 14px;
      font-weight: 400;
}
.day .dayNewsItem ul li {
        line-height: 20px;
        padding: 5px 0;
}
.day .dayNewsItem ul li a {
          color: #7e8c8d;
          text-decoration: none;
}



