
html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  overflow-x: hidden;
}
.mainView {
  height: 100%;
  width: 100%;
}
.mainView .navItemGroup .vux-no-group-title {
    margin-top: 0;
}
.mainView .navItemGroup .menuItem {
    font-size: 15px;
    color: #313131;
}
.loginName {
  text-align: center;
  color: #f0f0f0;
  font-weight: bold;
  margin-top: 20px;
}
.center {
  text-align: center;
  padding-top: 20px;
  color: #fff;
  font-size: 18px;
}
.center .logo {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 4px solid #ececec;
}



